/* Общие стили */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f9f9f9;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Шапка */
.header {
  background-color: #333;
  color: #fff;
  padding: 1rem 0;
}

.nav {
  display: flex;
  justify-content: center;
}

.nav-list {
  list-style: none;
  display: flex;
  gap: 1rem;
  margin: 0;
  padding: 0;
}

.nav-item a {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}

.nav-item a:hover {
  text-decoration: underline;
}

/* Основной контент */
.main-content {
  flex: 1;
  padding: 2rem;
  text-align: center;
}

.section {
  margin-bottom: 2rem;
}

.about-image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}

/* Футер */
.footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 1rem 0;
}

/* Адаптивность */
@media (max-width: 768px) {
  .nav-list {
    flex-direction: column;
    gap: 0.5rem;
  }

  .main-content {
    padding: 1rem;
  }
}